import { Module } from 'vuex';
import { RootState } from '../index'; // Root Vuex Store  RootState 
import router from '@/router';
import api from '@/api/api';
import axios from 'axios';
// ATC   
interface ATCState {
  // ATC  
  ingredient: Ingredient [];
  ingredientsymnonym: IngredientSymnonym [];
  seasoning: Seasoning [];
  seasoningsymnonym: SeasoningSymnonym [];
  allergyingredient: AllergyIngredient [];
  allergy: Allergy [];
  calendar: Calendar [];
  utensil: Utensil [];
  refregirator : Refregirator [];
  searchData: SearchData[];
  appendList: AppendList[];
}

// 
interface Ingredient {
  ingredient_id: number
  title: string
  kor: string
  eng: string
  def: string
  level: number
  upperClass: string
  superUpperClass: string
  etc: string
}

//  
interface IngredientSymnonym {
  ingredient_symnonym_id: number
  word: string
  ingredient_id: number
  code: any
}

// 
interface Seasoning {
  seasoning_id: number
  name: string
  kor: string
  eng: string
  definition: string
  level: number
  upperClass: string
  superUpperClass: string
  etc: string
}

//  
interface SeasoningSymnonym {
  seasoning_symnonym: number
  word: string
  seasoning_id: number
  code: any
}

//  
interface AllergyIngredient {
  allergy_ingredient_id: number
  allergy_id: number
  ingredient_id2: number
}

// 
interface Allergy {
  allergy_id: number
  name: string
}

// 
interface Calendar {
  calendar_id: number
  member_id: number
  recipe_id: number
  date: Date
  seq: number
}

// 
interface Utensil{
  utensil_id: number
  name: string
  way: number
}

// 
interface Refregirator {
  refregirator_id: number
  member_id: number
  ingredients_id: number
  storage: number
  // DECIMAL
  amount: string
  unit: string
  start_date: Date
  expired_date: Date
}

//   
interface SearchData{
  ingredientId: number
  name: String
}

//   
interface AppendList{
  ingredientId: number
}

const atc: Module<ATCState, RootState> = {
  state: {
    ingredient: [],
    ingredientsymnonym: [],
    seasoning: [],
    seasoningsymnonym: [],
    allergyingredient: [],
    allergy: [],
    calendar: [],
    utensil: [],
    refregirator: [],
    searchData: [],
    appendList:[],
  },
  getters: {
    ingredient: state => state.ingredient,
    seasoning: state => state.seasoning,
    searchData: state => state.searchData
    // ATC  
  },
  mutations: {
    GET_INGREDIENT: (state, ingredient) => (state.ingredient = ingredient),
    // ATC  
    SET_SEARCH_DATA: (state, data) => (state.searchData = data),
    SET_APPEND_LIST: (state, item) => (state.appendList.push(item)),
  },
  actions: {
    fetchIngredient({ commit, getters }) {
      axios({
        url: api.atc.ingredient(),
        method: 'get',
        // headers: getters.authHeader,
      })
        .then(res => {
          commit('GET_INGREDIENT', res.data);
        })
        .catch(err => console.log(err.response));
    },
    async specificIngredient({ commit, getters }, name) {
      try {
        console.log('  !', name);
        const response = await axios.get('https://i9b202.p.ssafy.io/api/foods/ingredient/search', {
          params: {
            name: name,
          },
        });
  
        console.log('   !', response.data);
        commit('SET_SEARCH_DATA', response.data);
        
      } catch (error) {
        console.error('   !', error);
      }
    },

    //  
    appendIngredient({commit}, item) {
      console.log(item,'!')
      commit('SET_APPEND_LIST', item)
    },

    //  (   )
    removeIngredient({commit}, item) {
      //  !
    }
  },
};

export default atc;
