import { Module } from 'vuex';
import { RootState } from '../index'; // Root Vuex Store  RootState 
import router from '@/router/index';
import api from '@/api/api';
import axios from 'axios';
import { AnyObject } from 'chart.js/dist/types/basic';

interface RecipeState {
  recipe: Recipe[];
  recipeSequence: RecipeSequence[];
  recipeUtensil: RecipeUtensil[];
  recipeSeasoning: RecipeSeasoning[];
  recipeIngredient: RecipeIngredient[];
  recipeSpecific: RecipeSpecific[];
  recipeDetail: RecipeDetail[];
  recipeRecommend: RecipeRecommend[];
}

// 
interface Recipe {
  recipe_id: number
  name: string
  // way: number
  // weight: number
  // calorie: number
  // carbohydrate: number
  // protein: number
  // fat: number
  // salt: number
  // tag: string
  img_small: string
  img_big: string
  category: string
  // dish: number
  likes: number
}
// 
interface RecipeSequence {
  procedure_id:number
  recipe_id: number
  sequence: number
  description: string
  img: string
}

//  
interface RecipeUtensil {
  recipe_utensil_id: number
  recipe_id: number
  utensil_id: number
}

//   
interface RecipeSeasoning {
  seasoning_inclusion_id: number
  recipe_key: number
  seasoning_id: number
}

//  
interface RecipeIngredient {
  ingredient_inclusion_id: number
  recipe_key: number
  ingredients_key: number
  type: number
  amount: number
  unit: string
  etc?: string
}

//  
interface RecipeSpecific {
  recipe_id: number
  name: string
  img: string
}

//  
interface RecipeDetail {
  name: string
  way: number
  weight: number
  calorie: number
  carbohydrate: number
  protein: number
  fat: number
  salt: number
  img: string
  // recipe_id: number
  // tag: string
  // img_small: string
  // img_big: string
  // category: string
  // dish: number
}

//   
interface RecipeRecommend {
  ingredient: Array<{
    ingredientId: number; 
  }>;
  seasoning: Array<{
    seasoningId: number; 
  }>;
  taste: Array<{
    ingredientId: number; 
  }>;
}


const recipe: Module<RecipeState, RootState> = {
  state: {
    recipe: [],
    recipeSpecific: [],
    recipeSequence: [],
    recipeUtensil: [],
    recipeSeasoning: [],
    recipeIngredient: [],
    recipeDetail: [],
    recipeRecommend: [],
  },

  getters: {
    recipe: state => state.recipe,
    recipeSpecific: state => state.recipeSpecific,
    recipeDetail: state => state.recipeDetail,
  },

  mutations: {
    SET_RECIPE: (state, recipe) => (state.recipe = recipe),
    SET_RECIPE_SPECIFIC: (state, recipeSpecific) => (state.recipeSpecific = recipeSpecific),
    SET_RECIPE_DETAIL: (state, recipeDetail) => (state.recipeDetail = recipeDetail),
  },

  actions: {
    //   
    fetchRecipe ({commit}) {
      console.log('get ')
      axios ({
        url:api.recipe.fetchRecipe(),
        method: 'get',
        // headers: getters.authHeader,
      })
        .then(res=> {
          console.log(res)
          commit('SET_RECIPE', res.data)
        })
        .catch(err => {
          console.log(err.response)
        })
    },

    //   
    recipeSpecificSearch ({commit}, content) {
      console.log(content, '   !')
      console.dir(api.recipe.specificRecipe()),
      axios ({
        //url: api.recipe.specificRecipe(),
        url: `https://i9b202.p.ssafy.io/api/recipes/want`,
        method: 'get',
        params: {
          content: content 
        },
        
      })
      .then (res=> {
        console.log('   !')
        console.log(res)
        commit('SET_RECIPE_SPECIFIC', res.data)
        router.push({
          name: 'searchKeyword',
          params: {
            keyword: content,
          },
        });
      })
      .catch(err => {
        console.log('   ..')
        console.log(err.response)
      })
    },

    //   
    detailRecipe ({commit}, recipe_id) {
      console.log('   !', recipe_id)
      axios({
        // url: api.recipe.detailRecipe(recipe_id),
        url: `https://i9b202.p.ssafy.io/api/recipes/${recipe_id}`,
        method:'get',

        // headers: getters.authHeader,
      })
        .then(res=> {
          console.log(res.data, '   !')
          commit('SET_RECIPE_DETAIL', res.data)
        })
        .catch(err => {
          console.log('   ....')
          console.log(err.response)
        })
    }
  },

}

export default recipe;
